/* styles for header */
.header-wrapper {
  width: 100%;
  position: fixed;
  padding: 0 6rem;
  background: white;
  z-index: 9;
}
.left-nav-wrapper {
  justify-content: space-between;
}
.left-nav-wrapper img {
  width: 4rem;
  margin-left: 1.4rem;
}
.header-wrapper .right-nav-wrapper {
  margin: 0;
  list-style: none;
  padding: 0.6rem 1rem;
}
.header-wrapper .right-nav-wrapper .navigation-link {
  font-weight: bold;
  font-size: 0.8rem;
  cursor: pointer;
  padding: 0.4rem 1.4rem;
  border-radius: 2px;
  text-transform: uppercase;
}
.header-wrapper .right-nav-wrapper .navigation-link:hover a {
  color: #fff;
}

.header-wrapper .right-nav-wrapper .navigation-link:hover {
  background: #000;
  color: #fff;
  transition: background 0.4s, color 0.4s;
}

/* styles for home */
.home-wrapper {
  padding: 8vh 150px 0;
  background-image: linear-gradient(#fff, #0606060d);
}
.left-home-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.left-home-wrapper .social-media-wrapper {
  display: flex;
}
.left-home-wrapper .social-media-wrapper .fa {
  display: inline-block;
  padding: 0.3rem;
  font-size: 2rem;
}
.left-home-wrapper .social-media-wrapper a {
  color: #000;
}

.left-home-wrapper .social-media-wrapper li {
  padding: 1rem 2rem 0 0;
}

.left-home-wrapper .social-media-wrapper li:hover .fa {
  background: #000;
  border-radius: 6px;
  color: #fff;
  transition: background 0.4s, color 0.4s;
}
.left-home-wrapper .social-media-wrapper a:hover {
  color: #000;
}
.left-home-wrapper .programming-wrapper {
  margin-top: 12px;
  margin-bottom: 1rem;
}

.programming-wrapper .fa {
  font-size: 1.4rem;
}
.programming-wrapper h5 {
  margin-left: 10px;
  margin-bottom: 0;
}

.right-home-wrapper img {
  height: 92vh;
  width: auto;
}

.right-home-wrapper .profile-image {
  background-image: url("./assets/profile-home.png");
  background-repeat: no-repeat;
  height: 92vh;
  width: 100%;
  z-index: 1111111;
}

/* styles for about section */
.about-wrapper {
  padding-left: 100px;
  margin-top: 120px;
}

.about-wrapper .right-wrapper {
    padding-right: 180px;
    padding-left: 20px;
}
.about-wrapper .left-wrapper img {
  width: 450px;
  border-radius: 2px;
}
.about-wrapper .right-wrapper p {
  text-align: justify;
}
.about-wrapper .technology-wrapper {
  border-top: 1px solid #d4d4d47a;
  border-bottom: 1px solid #d4d4d47a;
}
.about-wrapper .technology-wrapper .fa {
  display: inline-block;
  margin-right: 0.5rem;
  font-size: 0.6rem;
}
.about-wrapper .technology-wrapper ul {
  display: flex;
  font-size: 0.8rem;
  font-weight: bold;
}
.about-wrapper .technology-wrapper h6 {
  color: #dd2c00;
}
.about-wrapper .technology-wrapper ul li {
  margin-bottom: 0.2rem;
}
.about-wrapper .technology-wrapper ul li a {
  color: #000;
}
.about-wrapper .technology-wrapper ul li a:hover {
  color: #dd2c00;
  transition: color 0.4s;
}

/* styles for project section */
.project-details-wrapper {
  justify-content: center;
}
.project-details-wrapper .cards {
  position: relative;
  cursor: pointer;
}
.project-details-wrapper .cards .card-front {
  position: absolute;
  width: 100%;
  height: 18rem;
  padding: 8px;
  visibility: visible;
  opacity: 1;
  transition: opacity 1s, visibility 1s;
}
.project-details-wrapper .cards .card-back img {
  width: 3rem;
  height: 3rem;
}
.project-details-wrapper .cards:hover .card-front {
  visibility: hidden;
  opacity: 0;
}
.project-details-wrapper .cards:hover .card-back {
  visibility: visible;
  opacity: 1;
}
.project-details-wrapper .cards .card-back {
  padding: 0 40px 20px;
  visibility: hidden;
  opacity: 0;
  transition: opacity 1s, visibility 1s;
}
.project-details-wrapper .cards .card-back .fa {
  font-size: 3rem;
  color: #dd2c00;
}
.project-details-wrapper .cards .card-back h3 {
  margin: 0.4rem 0 0.6rem;
  font-size: 1rem;
  font-weight: bold;
  color: #dd2c00;
}
.project-details-wrapper .cards .card-back p {
  font-size: 0.9rem;
  text-align: center;
}

/* styles for service section */
.services-details-wrapper .slide-wrapper {
  position: relative;
  height: 16rem;
  margin-top: 20px;
}
.services-details-wrapper .slide-wrapper .slide {
  display: flex;
  align-items: center;
  width: 100%;
  margin-top: 3rem;
  box-shadow: 0 3rem 6rem rgb(0 0 0 / 10%);
  background-color: #55c57a;
  border-radius: 3px;
  padding: 4rem 6rem;
  font-size: 1.6rem;
  cursor: pointer;
  transition: all 0.8s ease;
  position: absolute;
  top: 0;
  bottom: 0;
  backface-visibility: hidden;
  overflow: hidden;
}

.services-details-wrapper .slide .icon-wrapper {
  width: 5rem;
  height: 5rem;
  border-radius: 50%;
  cursor: pointer;
  transform: translateX(-3rem) skewX(12deg);
  background: #000;
}

.services-details-wrapper .slide .content-wrappwer {
  transform: translateX(-3rem) skewX(12deg);
  margin-left: 1rem;
}
.services-details-wrapper .slide p {
  font-size: 0.8rem;
  letter-spacing: 0.05rem;
  font-weight: bold;
  font-family: "Sansita-Bold";
  text-transform: uppercase;
  color: #000;
}
.services-details-wrapper .slide .fa {
  font-size: 2rem;
  color: #fff;
}
.services-details-wrapper .slide .dev-info {
  font-size: 1rem;
  color: #fff;
}
.services-details-wrapper .slide .btn-wrapper {
  justify-content: flex-start;
  width: 100%;
  margin-top: 1rem;
}
.services-details-wrapper .slide .btn-wrapper .btn-std {
  background: #000;
  color: #fff;
  border: none;
}

.services-details-wrapper .slide .dev-info h4 {
  font-size: 0.9rem;
  font-weight: bold;
}

.services-details-wrapper .slide .dev-info li {
  align-items: center;
  display: flex;
  align-items: center;
  font-size: 0.8rem;
  margin-bottom: 0.2rem;
}
.services-details-wrapper .slide .dev-info ul .fa {
  display: inline-block;
  margin-right: 0.5rem;
  color: #dd2c00;
  font-size: 0.4rem;
}

.services-details-wrapper .slide .icon-wrapper:hover .fa {
  color: #fff;
}
.services-details-wrapper .slide .icon-wrapper:hover {
  background: #000;
}

.services-details-wrapper .slide-wrapper .card-front {
  transform: skewX(-12deg);
}
.services-details-wrapper .slide-wrapper .card-back {
  transform: rotateY(-180deg);
  background: #fff;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.services-details-wrapper .slide-wrapper:hover .dev-info {
  transform: translateX(-3rem) skewX(12deg);
}
.services-details-wrapper .slide-wrapper:hover .btn-wrapper {
  transform: translateX(-3rem) skewX(12deg);
}
.services-details-wrapper .slide-wrapper:hover .card-front {
  transform: rotateY(-180deg);
}

.services-details-wrapper .slide-wrapper:hover .card-back {
  transform: skewX(-12deg);
}

/* styles for contact section */
.contact-details-wrapper {
  justify-content: center;
}

.contact-details-wrapper .cards .fa {
  font-size: 2rem;
  color: #dd2c00;
}

.contact-details-wrapper .cards h5 {
  font-size: 1.4rem;
  font-weight: 700;
  margin-top: 1.2rem;
}
.contact-details-wrapper .cards p {
  font-size: 1rem;
}

/* styles for footer */
.footer-wrapper {
  width: 100%;
  padding: 10px 0;
  margin: 100px 0 20px;
}
.footer-wrapper img {
  width: 15px;
  height: 15px;
}

.footer-wrapper .fa {
  font-size: 1rem;
  padding: 0.1rem;
  color: #55c57a;
}

.footer-wrapper .lang {
  color: #55c57a;
  font-weight: bold;
}

@media only screen and (max-width: 56.25em) {
  .display-mobile-hide {
    display: none;
  }
  .display-mobile-block {
    display: block;
  }
  .header-mb-1 {
    font-size: 3rem;
  }
  .header-mb-2 {
    font-size: 2rem;
    font-weight: bold;
    font-family: "Sansita-Bold";
  }
  .header-mb-3 {
    font-size: 1rem;
  }
  /* media queires for home  */
  .header-wrapper {
    justify-content: flex-start;
    padding: 1rem 0.2rem;
  }
  .header-wrapper .left-nav-mobile-wrapper img {
    position: absolute;
    right: 80px;
    top: -4px;
    z-index: 1111;
    width: 4rem;
  }
  .header-wrapper .right-nav-wrapper .navigation-link {
    padding: 1.4rem 0;
    font-size: 0.8rem;
    width: 100%;
  }
  .header-wrapper .right-nav-wrapper .navigation-link button {
    width: 100%;
    padding: 0.8rem 8rem;
    margin-top: 1rem;
  }
  .header-wrapper .right-nav-wrapper .navigation-link .fa {
    font-size: 1rem;
    padding-right: 1.6rem;
    margin-left: 20px;
    /* color: #dd2c00; */
  }
  .header-wrapper .right-nav-wrapper .navigation-link:last-child .fa {
    margin-left: 0;
  }
  .header-wrapper .right-nav-wrapper .navigation-link:last-child {
    display: flex;
    justify-content: center;
  }
  .header-wrapper .right-nav-wrapper .navigation-link:hover .fa {
    color: #fff;
  }
  .header-wrapper .right-nav-wrapper .navigation-link .btn-std:hover .fa-file {
    color: #fff;
  }
  .header-wrapper .right-nav-wrapper .navigation-link:last-child:hover {
    background: #fff;
  }
  /* media queires for home  */
  .home-wrapper {
    position: relative;
    padding: 8rem 0 0;
  }

  .left-home-wrapper .header-sm-mt-2 {
    margin-top: 3rem;
  }
  .home-wrapper .left-home-wrapper .programming-wrapper {
    margin-bottom: 3rem;
    margin-left: 0;
  }
  .left-home-wrapper .social-media-wrapper li {
    padding: 1rem;
  }
  .left-home-wrapper .social-media-wrapper li:first-child {
    padding-left: 0;
  }
  .right-home-wrapper img {
    height: 350px;
  }

  /* styles for about section */
  .about-wrapper {
    padding-left: 0;
  }

  .about-wrapper .left-wrapper img {
    height: 160px;
    width: 160px;
    border-radius: 50%;
    box-shadow: 0 3rem 6rem rgb(0 0 0 / 30%);
    border: 0.1rem solid #f9fafb;
  }
  .about-wrapper .right-wrapper {
    padding-right: 0.8rem;
  }
  .about-wrapper .technology-wrapper ul li {
    margin-left: 0.5rem;
    margin-top: 0.5rem;
  }
  .about-wrapper .technology-wrapper ul {
    flex-direction: column;
  }

  /* styles for service section */
  .service-wrapper {
    padding: 0 0.6rem;
  }
  .service-wrapper .services-details-wrapper {
    padding: 0 1.4rem;
  }

  /* styles for project section */
  .project-wrapper {
    padding: 0 0.6rem;
  }

  /* styles for project section */
  .contact-wrapper {
    padding: 0 0.6rem;
  }

  /* stylef for hamburger menu */
  #menuToggle {
    display: block;
    position: relative;
    top: 0;
    left: 10px;

    z-index: 1;

    user-select: none;
  }
  #menuToggle input {
    display: block;
    width: 40px;
    height: 32px;
    position: absolute;
    top: -7px;
    left: -5px;

    cursor: pointer;

    opacity: 0; /* hide this */
    z-index: 2; /* and place it over the hamburger */
  }

  /*
 * Just a quick hamburger
 */
  #menuToggle .icon {
    display: block;
    width: 22px;
    height: 2px;
    margin-bottom: 5px;
    position: relative;

    background: #000;
    border-radius: 3px;

    z-index: 1;

    transform-origin: -1px 1px;

    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1),
      background 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
  }

  #menuToggle .icon:first-child {
    transform-origin: 0% 0%;
  }

  #menuToggle .icon:nth-last-child(2) {
    transform-origin: 0% 100%;
  }
  /* 
* Transform all the slices of hamburger
* into a crossmark.
*/
  #menuToggle input:checked ~ .icon {
    opacity: 1;
    transform: rotate(45deg) translate(-2px, -1px);
    background: #000;
  }

  /*
* But let's hide the middle one.
*/
  #menuToggle input:checked ~ .icon:nth-last-child(3) {
    opacity: 0;
    transform: rotate(0deg) scale(0.2, 0.2);
  }

  /*
 * Ohyeah and the last one should go the other direction
 */
  #menuToggle input:checked ~ .icon:nth-last-child(2) {
    transform: rotate(-45deg) translate(0, -1px);
  }

  /*
 * Make this absolute positioned
 * at the top left of the screen
 */
  #menu {
    position: absolute;
    width: 22rem;
    margin: -100px 0 0 -30px;
    padding: 50px 0 50px 10px;
    padding-top: 125px;

    background: #fff;
    list-style-type: none;
    box-shadow: 0 1.5rem 4rem rgb(0 0 0 / 15%);
    /* to stop flickering of text in safari */

    transform-origin: 0% 0%;
    transform: translate(-100%, 0);

    transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
    flex-direction: column;
    display: flex;
    align-items: flex-start;
    height: calc(100vh + 100px);
  }

  /*
 * And let's slide it in from the left
 */
  #menuToggle input:checked ~ ul {
    transform: none;
  }
}
